import { type NextRequest, NextResponse } from 'next/server';
import { uploadToCloudinary } from '@/lib/cloudinary';
import { writeFile, mkdir } from 'fs/promises';
import { existsSync } from 'fs';
import path from 'path';

// ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ™ŸàŸÅÿ± ÿ•ÿπÿØÿßÿØÿßÿ™ Cloudinary
const isCloudinaryAvailable = Boolean(
  process.env.CLOUDINARY_CLOUD_NAME &&
  process.env.CLOUDINARY_API_KEY &&
  process.env.CLOUDINARY_API_SECRET &&
  process.env.CLOUDINARY_CLOUD_NAME !== 'demo'
);

// ÿØÿßŸÑÿ© ŸÑÿ™ÿ≠ÿ≥ŸäŸÜ ÿ£ÿ≥ŸÖÿßÿ° ÿßŸÑŸÖŸÑŸÅÿßÿ™ ŸÑŸÄ SEO
function generateSEOFriendlyName(originalName: string): string {
  const nameWithoutExt = originalName.replace(/\.[^/.]+$/, '');
  return nameWithoutExt
    .toLowerCase()
    .replace(/[\s_]+/g, '-') // ÿßÿ≥ÿ™ÿ®ÿØÿßŸÑ ÿßŸÑŸÖÿ≥ÿßŸÅÿßÿ™ ÿ®ŸÄ -
    .replace(/[^\w\u0600-\u06FF-]/g, '') // ÿßŸÑÿ≠ŸÅÿßÿ∏ ÿπŸÑŸâ ÿßŸÑÿ£ÿ≠ÿ±ŸÅ ÿßŸÑÿπÿ±ÿ®Ÿäÿ© ŸàÿßŸÑÿ•ŸÜÿ¨ŸÑŸäÿ≤Ÿäÿ© ŸÅŸÇÿ∑
    .replace(/-+/g, '-') // ÿ•ÿ≤ÿßŸÑÿ© ÿßŸÑÿ¥ÿ±ÿ∑ÿßÿ™ ÿßŸÑŸÖÿ™ŸÉÿ±ÿ±ÿ©
    .replace(/^-|-$/g, '') // ÿ•ÿ≤ÿßŸÑÿ© ÿßŸÑÿ¥ÿ±ÿ∑ÿßÿ™ ŸÖŸÜ ÿßŸÑÿ®ÿØÿßŸäÿ© ŸàÿßŸÑŸÜŸáÿßŸäÿ©
    .substring(0, 50) || 'aldeyar-project'; // ÿ™ÿ≠ÿØŸäÿØ ÿßŸÑÿ∑ŸàŸÑ
}

// ÿ•ÿπÿØÿßÿØÿßÿ™ ŸÖÿ≠ÿ≥ŸÜÿ© ŸÑŸÑÿ±ŸÅÿπ
const UPLOAD_CONFIG = {
  maxFileSize: isCloudinaryAvailable ? 100 * 1024 * 1024 : 50 * 1024 * 1024, // 100MB/50MB
  maxFiles: 20, // ÿ≠ÿØ ÿ£ŸÇÿµŸâ 20 ŸÖŸÑŸÅ ŸÅŸä ÿßŸÑŸÖÿ±ÿ© ÿßŸÑŸàÿßÿ≠ÿØÿ©
  allowedImageTypes: [
    'image/jpeg', 'image/jpg', 'image/png', 'image/webp', 
    'image/gif', 'image/bmp', 'image/tiff'
  ],
  allowedVideoTypes: [
    'video/mp4', 'video/mov', 'video/avi', 'video/webm', 
    'video/quicktime', 'video/x-msvideo', 'video/mkv'
  ],
  compressionQuality: {
    image: { quality: 85, width: 1920, height: 1080 },
    video: { quality: 'auto', width: 1280, height: 720 }
  }
};

export async function POST(request: NextRequest) {
  try {
    const formData = await request.formData();

    // ÿØÿπŸÖ ÿ±ŸÅÿπ ŸÖŸÑŸÅ Ÿàÿßÿ≠ÿØ ÿ£Ÿà ÿπÿØÿ© ŸÖŸÑŸÅÿßÿ™
    const files = formData.getAll('files') as File[];
    const singleFile = formData.get('file') as File;

    console.log('üì§ ŸÖÿ≠ÿßŸàŸÑÿ© ÿ±ŸÅÿπ ŸÖŸÑŸÅ:', {
      singleFile: !!singleFile,
      singleFileName: singleFile?.name,
      singleFileType: singleFile?.type,
      singleFileSize: singleFile?.size,
      multipleFiles: files.length,
      cloudinaryAvailable: isCloudinaryAvailable
    });

    let filesToProcess: File[] = [];

    if (singleFile && singleFile.size > 0) {
      filesToProcess = [singleFile];
    } else if (files && files.length > 0) {
      // ÿ™ÿµŸÅŸäÿ© ÿßŸÑŸÖŸÑŸÅÿßÿ™ ÿßŸÑŸÅÿßÿ±ÿ∫ÿ©
      filesToProcess = files.filter(file => file && file.size > 0);
    }

    if (filesToProcess.length === 0) {
      console.log('‚ùå ŸÑŸÖ Ÿäÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿØ ÿ£Ÿä ŸÖŸÑŸÅÿßÿ™');
      return NextResponse.json(
        { error: 'ŸÑŸÖ Ÿäÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿØ ÿ£Ÿä ŸÖŸÑŸÅÿßÿ™' },
        { status: 400 }
      );
    }

    const uploadedFiles = [];

    // ÿ•ÿπÿØÿßÿØ ÿßŸÑÿ™ÿÆÿ≤ŸäŸÜ ÿßŸÑŸÖÿ≠ŸÑŸä ŸÉŸÄ fallback
    let uploadDir = '';
    if (!isCloudinaryAvailable) {
      uploadDir = path.join(process.cwd(), 'public', 'uploads');
      if (!existsSync(uploadDir)) {
        await mkdir(uploadDir, { recursive: true });
        console.log('üìÅ ÿ™ŸÖ ÿ•ŸÜÿ¥ÿßÿ° ŸÖÿ¨ŸÑÿØ uploads (fallback mode)');
      }
    }

    for (const file of filesToProcess) {
      if (!file || file.size === 0) {
        console.log('‚ö†Ô∏è ÿ™ÿ¨ÿßŸáŸÑ ŸÖŸÑŸÅ ŸÅÿßÿ±ÿ∫');
        continue;
      }

      console.log('üîç ŸÖÿπÿßŸÑÿ¨ÿ© ŸÖŸÑŸÅ:', {
        name: file.name,
        type: file.type,
        size: file.size
      });

      // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ŸÜŸàÿπ ÿßŸÑŸÖŸÑŸÅ ŸÖÿπ ÿØÿπŸÖ ÿ£ŸÅÿ∂ŸÑ
      const isImage = UPLOAD_CONFIG.allowedImageTypes.includes(file.type);
      const isVideo = UPLOAD_CONFIG.allowedVideoTypes.includes(file.type);
      
      if (!isImage && !isVideo) {
        console.log('‚ùå ŸÜŸàÿπ ŸÖŸÑŸÅ ÿ∫Ÿäÿ± ŸÖÿØÿπŸàŸÖ:', file.type);
        uploadedFiles.push({
          originalName: file.name,
          error: `ŸÜŸàÿπ ÿßŸÑŸÖŸÑŸÅ ${file.type} ÿ∫Ÿäÿ± ŸÖÿØÿπŸàŸÖ`,
          type: 'ERROR'
        });
        continue;
      }

      // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ≠ÿ¨ŸÖ ÿßŸÑŸÖŸÑŸÅ ŸÖÿπ ÿ±ÿ≥ÿßÿ¶ŸÑ ŸÖŸÅÿµŸÑÿ©
      if (file.size > UPLOAD_CONFIG.maxFileSize) {
        const maxSizeMB = (UPLOAD_CONFIG.maxFileSize / 1024 / 1024).toFixed(0);
        const fileSizeMB = (file.size / 1024 / 1024).toFixed(1);
        console.log(`‚ùå ŸÖŸÑŸÅ ŸÉÿ®Ÿäÿ± ÿ¨ÿØÿßŸã: ${fileSizeMB}MB > ${maxSizeMB}MB`);
        uploadedFiles.push({
          originalName: file.name,
          error: `ÿ≠ÿ¨ŸÖ ÿßŸÑŸÖŸÑŸÅ ${fileSizeMB}MB Ÿäÿ™ÿ¨ÿßŸàÿ≤ ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ŸÇÿµŸâ ${maxSizeMB}MB`,
          type: 'ERROR'
        });
        continue;
      }

      // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿßŸÖÿ™ÿØÿßÿØ ÿßŸÑŸÖŸÑŸÅ ŸÑŸÑÿ£ŸÖÿßŸÜ
      const fileExtension = path.extname(file.name).toLowerCase();
      const validImageExts = ['.jpg', '.jpeg', '.png', '.webp', '.gif', '.bmp', '.tiff'];
      const validVideoExts = ['.mp4', '.mov', '.avi', '.webm', '.mkv'];
      
      if (isImage && !validImageExts.includes(fileExtension)) {
        uploadedFiles.push({
          originalName: file.name,
          error: `ÿßŸÖÿ™ÿØÿßÿØ ÿßŸÑŸÖŸÑŸÅ ${fileExtension} ŸÑÿß Ÿäÿ™ÿ∑ÿßÿ®ŸÇ ŸÖÿπ ŸÜŸàÿπ ÿßŸÑÿµŸàÿ±ÿ©`,
          type: 'ERROR'
        });
        continue;
      }
      
      if (isVideo && !validVideoExts.includes(fileExtension)) {
        uploadedFiles.push({
          originalName: file.name,
          error: `ÿßŸÖÿ™ÿØÿßÿØ ÿßŸÑŸÖŸÑŸÅ ${fileExtension} ŸÑÿß Ÿäÿ™ÿ∑ÿßÿ®ŸÇ ŸÖÿπ ŸÜŸàÿπ ÿßŸÑŸÅŸäÿØŸäŸà`,
          type: 'ERROR'
        });
        continue;
      }

      try {
        let uploadedFile;

        if (isCloudinaryAvailable) {
          // ÿ±ŸÅÿπ ÿ•ŸÑŸâ Cloudinary ŸÖÿπ ÿ•ÿπÿØÿßÿØÿßÿ™ ŸÖÿ≠ÿ≥ŸÜÿ©
          console.log('‚òÅÔ∏è ÿ±ŸÅÿπ ÿ•ŸÑŸâ Cloudinary...');
          
          // ÿ™ÿ≠ÿ≥ŸäŸÜ ÿ£ÿ≥ŸÖÿßÿ° ÿßŸÑŸÖŸÑŸÅÿßÿ™ ŸÑŸÄ SEO
          const seoFriendlyName = generateSEOFriendlyName(file.name);
          
          const cloudinaryOptions = {
            folder: 'portfolio/projects',
            resource_type: isVideo ? 'video' : 'image',
            public_id: `${Date.now()}-${seoFriendlyName}`,
            use_filename: false,
            unique_filename: true,
            overwrite: false,
            // ÿ•ÿ∂ÿßŸÅÿ© metadata ŸÑŸÑSEO
            context: {
              alt: `ŸÖÿ≠ÿ™ÿ±ŸÅŸäŸÜ ÿßŸÑÿØŸäÿßÿ± ÿßŸÑÿπÿßŸÑŸÖŸäÿ© - ${seoFriendlyName}`,
              caption: `ŸÖÿ¥ÿ±Ÿàÿπ ŸÖŸÜ ŸÖÿ≠ÿ™ÿ±ŸÅŸäŸÜ ÿßŸÑÿØŸäÿßÿ± ÿßŸÑÿπÿßŸÑŸÖŸäÿ© ŸÅŸä ÿ¨ÿØÿ©`,
              title: seoFriendlyName
            },
            // ÿ™ÿ≠ÿ≥ŸäŸÜ ÿÆÿµÿßÿ¶ÿµ ÿßŸÑŸÖŸÑŸÅÿßÿ™ ŸÑŸÑŸàŸäÿ®
            transformation: isVideo ? {
              // ÿ•ÿπÿØÿßÿØÿßÿ™ ÿ£ÿ≥ÿßÿ≥Ÿäÿ© ŸÑŸÑŸÅŸäÿØŸäŸà ŸÅŸÇÿ∑
              quality: 'auto',
              width: 1280,
              height: 720,
              crop: 'limit'
            } : {
              // ÿ™ÿ≠ÿ≥ŸäŸÜ ŸÑŸÑÿµŸàÿ±
              quality: 'auto',
              format: 'webp',
              width: 'auto',
              crop: 'scale',
              dpr: 'auto',
              flags: 'progressive'
            },
            // ÿ•ÿπÿØÿßÿØÿßÿ™ ÿ•ÿ∂ÿßŸÅŸäÿ© ŸÑŸÑÿ£ŸÖÿßŸÜ ŸàÿßŸÑÿ£ÿØÿßÿ°
            invalidate: true,
            overwrite: true,
            unique_filename: true,
            use_filename: false
          };

          const result = await uploadToCloudinary(file, cloudinaryOptions);

          console.log('‚úÖ ÿ™ŸÖ ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ ÿ•ŸÑŸâ Cloudinary:', result.secure_url);

          uploadedFile = {
            originalName: file.name,
            fileName: result.public_id,
            src: result.secure_url,
            url: result.secure_url,
            type: file.type.startsWith('video/') ? 'VIDEO' : 'IMAGE',
            size: result.bytes,
            mimeType: file.type,
            width: result.width || null,
            height: result.height || null,
            duration: result.duration || null,
            cloudinary_public_id: result.public_id,
            cloudinary_url: result.secure_url,
            resource_type: result.resource_type,
            storage_type: 'cloudinary'
          };

          // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿµÿ≠ÿ© ÿßŸÑŸÜÿ™Ÿäÿ¨ÿ©
          if (!uploadedFile.src) {
            throw new Error('ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿ≠ÿµŸàŸÑ ÿπŸÑŸâ ÿ±ÿßÿ®ÿ∑ ÿµÿ≠Ÿäÿ≠ ŸÖŸÜ Cloudinary');
          }

        } else {
          // ÿ±ŸÅÿπ ŸÖÿ≠ŸÑŸä (fallback)
          console.log('üíæ ÿ±ŸÅÿπ ŸÖÿ≠ŸÑŸä (fallback mode)...');

          const timestamp = Date.now();
          const randomString = Math.random().toString(36).substring(2, 15);
          const fileExtension = path.extname(file.name);
          const fileName = `${timestamp}_${randomString}${fileExtension}`;

          const filePath = path.join(uploadDir, fileName);
          const bytes = await file.arrayBuffer();
          const buffer = Buffer.from(bytes);

          await writeFile(filePath, buffer);
          console.log('‚úÖ ÿ™ŸÖ ÿ≠ŸÅÿ∏ ÿßŸÑŸÖŸÑŸÅ ŸÖÿ≠ŸÑŸäÿßŸã:', fileName);

          uploadedFile = {
            originalName: file.name,
            fileName: fileName,
            src: `/uploads/${fileName}`,
            url: `/uploads/${fileName}`,
            type: file.type.startsWith('video/') ? 'VIDEO' : 'IMAGE',
            size: file.size,
            mimeType: file.type,
            storage_type: 'local'
          };
        }

        uploadedFiles.push(uploadedFile);

      } catch (uploadError) {
        console.error('‚ùå ÿÆÿ∑ÿ£ ŸÅŸä ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ:', uploadError);

        uploadedFiles.push({
          originalName: file.name,
          error: `ŸÅÿ¥ŸÑ ŸÅŸä ÿ±ŸÅÿπ ${file.name}: ${uploadError instanceof Error ? uploadError.message : 'ÿÆÿ∑ÿ£ ÿ∫Ÿäÿ± ŸÖÿπÿ±ŸàŸÅ'}`,
          type: 'ERROR'
        });
      }
    }

    if (uploadedFiles.length === 0) {
      return NextResponse.json(
        { error: 'ŸÑŸÖ Ÿäÿ™ŸÖ ÿ±ŸÅÿπ ÿ£Ÿä ŸÖŸÑŸÅÿßÿ™ ÿ®ŸÜÿ¨ÿßÿ≠' },
        { status: 400 }
      );
    }

    // ŸÅÿµŸÑ ÿßŸÑŸÖŸÑŸÅÿßÿ™ ÿßŸÑŸÜÿßÿ¨ÿ≠ÿ© ÿπŸÜ ÿßŸÑÿÆÿßÿ∑ÿ¶ÿ©
    const successfulFiles = uploadedFiles.filter(file => file.type !== 'ERROR');
    const failedFiles = uploadedFiles.filter(file => file.type === 'ERROR');

    console.log('üìä ŸÜÿ™Ÿäÿ¨ÿ© ÿßŸÑÿ±ŸÅÿπ:', {
      successful: successfulFiles.length,
      failed: failedFiles.length,
      total: uploadedFiles.length,
      storageType: isCloudinaryAvailable ? 'cloudinary' : 'local'
    });

    return NextResponse.json({
      success: true,
      message: `ÿ™ŸÖ ÿ±ŸÅÿπ ${successfulFiles.length} ŸÖŸÑŸÅ ÿ®ŸÜÿ¨ÿßÿ≠${failedFiles.length > 0 ? ` Ÿà ŸÅÿ¥ŸÑ ${failedFiles.length} ŸÖŸÑŸÅ` : ''}`,
      files: successfulFiles,
      errors: failedFiles.length > 0 ? failedFiles : undefined,
      count: successfulFiles.length,
      storage_type: isCloudinaryAvailable ? 'cloudinary' : 'local'
    });

  } catch (error) {
    console.error('‚ùå ÿÆÿ∑ÿ£ ÿπÿßŸÖ ŸÅŸä ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅÿßÿ™:', error);
    return NextResponse.json(
      {
        error: 'ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ŸÅŸä ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅÿßÿ™',
        details: error instanceof Error ? error.message : 'ÿÆÿ∑ÿ£ ÿ∫Ÿäÿ± ŸÖÿπÿ±ŸàŸÅ'
      },
      { status: 500 }
    );
  }
}
