import { v2 as cloudinary } from 'cloudinary';

// ÿ™ŸÉŸàŸäŸÜ Cloudinary
const isCloudinaryConfigured = Boolean(
  process.env.CLOUDINARY_CLOUD_NAME &&
  process.env.CLOUDINARY_API_KEY &&
  process.env.CLOUDINARY_API_SECRET &&
  process.env.CLOUDINARY_CLOUD_NAME !== 'demo'
);

if (isCloudinaryConfigured) {
  cloudinary.config({
    cloud_name: process.env.CLOUDINARY_CLOUD_NAME,
    api_key: process.env.CLOUDINARY_API_KEY,
    api_secret: process.env.CLOUDINARY_API_SECRET,
  });
  console.log('‚úÖ Cloudinary configured successfully with cloud:', process.env.CLOUDINARY_CLOUD_NAME);
} else {
  console.log('‚ö†Ô∏è Cloudinary not configured. Using local fallback mode.');
  console.log('üìã Environment variables status:', {
    cloud_name: process.env.CLOUDINARY_CLOUD_NAME || 'missing',
    api_key: process.env.CLOUDINARY_API_KEY ? 'configured' : 'missing',
    api_secret: process.env.CLOUDINARY_API_SECRET ? 'configured' : 'missing'
  });
}

export interface CloudinaryUploadResult {
  public_id: string;
  secure_url: string;
  url: string;
  format: string;
  resource_type: 'image' | 'video' | 'raw';
  bytes: number;
  width?: number;
  height?: number;
  duration?: number;
  created_at: string;
}

/**
 * ÿ±ŸÅÿπ ŸÖŸÑŸÅ ÿ•ŸÑŸâ Cloudinary
 */
export async function uploadToCloudinary(
  file: File,
  options: {
    folder?: string;
    public_id?: string;
    resource_type?: 'auto' | 'image' | 'video' | 'raw';
    transformation?: any;
  } = {}
): Promise<CloudinaryUploadResult> {
  // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ•ÿπÿØÿßÿØ Cloudinary
  if (!isCloudinaryConfigured) {
    throw new Error('Cloudinary ÿ∫Ÿäÿ± ŸÖŸèÿπŸéÿØŸë. Ÿäÿ±ÿ¨Ÿâ ÿ•ÿ∂ÿßŸÅÿ© ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑÿßÿπÿ™ŸÖÿßÿØ ŸÅŸä ŸÖŸÑŸÅ .env');
  }

  try {
    // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿµÿ≠ÿ© ÿßŸÑŸÖŸÑŸÅ ÿ£ŸàŸÑÿßŸã
    if (!file || file.size === 0) {
      throw new Error('ÿßŸÑŸÖŸÑŸÅ ŸÅÿßÿ±ÿ∫ ÿ£Ÿà ÿ∫Ÿäÿ± ÿµÿßŸÑÿ≠');
    }

    // ÿ™ÿ≠ŸàŸäŸÑ File ÿ•ŸÑŸâ Buffer ŸÖÿπ ŸÖÿπÿßŸÑÿ¨ÿ© ÿßŸÑÿ£ÿÆÿ∑ÿßÿ°
    let buffer: Buffer;
    try {
      const bytes = await file.arrayBuffer();
      buffer = Buffer.from(bytes);

      if (buffer.length === 0) {
        throw new Error('ŸÅÿ¥ŸÑ ŸÅŸä ŸÇÿ±ÿßÿ°ÿ© ŸÖÿ≠ÿ™ŸàŸâ ÿßŸÑŸÖŸÑŸÅ');
      }
    } catch (error) {
      throw new Error(`ŸÅÿ¥ŸÑ ŸÅŸä ŸÖÿπÿßŸÑÿ¨ÿ© ÿßŸÑŸÖŸÑŸÅ: ${error instanceof Error ? error.message : 'ÿÆÿ∑ÿ£ ÿ∫Ÿäÿ± ŸÖÿπÿ±ŸàŸÅ'}`);
    }

    // ÿ™ÿ≠ÿØŸäÿØ ŸÜŸàÿπ ÿßŸÑŸÖŸÑŸÅ ŸÖÿπ ŸÅÿ≠ÿµ ÿ£ÿπŸÖŸÇ
    const isVideo = file.type.startsWith('video/');
    const isImage = file.type.startsWith('image/');

    if (!isVideo && !isImage) {
      throw new Error(`ŸÜŸàÿπ ÿßŸÑŸÖŸÑŸÅ ÿ∫Ÿäÿ± ŸÖÿØÿπŸàŸÖ: ${file.type}`);
    }

    const defaultOptions = {
      folder: options.folder || 'portfolio',
      resource_type: options.resource_type || (isVideo ? 'video' : isImage ? 'image' : 'auto'),
      public_id: options.public_id,
      // ÿ™ÿ≠ÿ≥ŸäŸÜÿßÿ™ ŸÖÿÆÿµÿµÿ© ÿ≠ÿ≥ÿ® ŸÜŸàÿπ ÿßŸÑŸÖŸÑŸÅ
      transformation: options.transformation || (isVideo ? {
        // ÿ•ÿπÿØÿßÿØÿßÿ™ ÿ£ÿ≥ÿßÿ≥Ÿäÿ© ŸÑŸÑŸÅŸäÿØŸäŸà
        quality: 'auto',
        width: 1280,
        height: 720,
        crop: 'limit',
        bit_rate: '1m'
      } : {
        // ÿ™ÿ≠ÿ≥ŸäŸÜÿßÿ™ ŸÑŸÑÿµŸàÿ±
        quality: 'auto',
        fetch_format: 'auto',
        flags: 'progressive',
      }),
      // ÿ•ÿπÿØÿßÿØÿßÿ™ ÿ•ÿ∂ÿßŸÅŸäÿ©
      overwrite: true,
      invalidate: true,
      // ÿ•ÿπÿØÿßÿØÿßÿ™ ÿÆÿßÿµÿ© ÿ®ÿßŸÑŸÅŸäÿØŸäŸà
      ...(isVideo && {
        chunk_size: 6000000, // 6MB chunks for large videos
        timeout: 120000, // 2 minutes timeout for videos
      }),
      ...options
    };

    console.log('üöÄ ÿ±ŸÅÿπ ŸÖŸÑŸÅ ÿ•ŸÑŸâ Cloudinary:', {
      name: file.name,
      type: file.type,
      size: file.size,
      folder: defaultOptions.folder,
      resource_type: defaultOptions.resource_type,
      sizeInMB: (file.size / 1024 / 1024).toFixed(2) + 'MB'
    });

    // ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ
    const result: { secure_url: string; public_id: string } = await new Promise((resolve, reject) => {
      cloudinary.uploader.upload_stream(
        defaultOptions,
        (error, result) => {
          if (error) {
            console.error('‚ùå ÿÆÿ∑ÿ£ ŸÅŸä ÿ±ŸÅÿπ Cloudinary:', error);
            reject(error);
          } else if (result) {
            console.log('‚úÖ ÿ™ŸÖ ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ ÿ®ŸÜÿ¨ÿßÿ≠:', {
              url: result.secure_url,
              public_id: result.public_id,
              resource_type: result.resource_type,
              bytes: result.bytes
            });

            // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿµÿ≠ÿ© ÿßŸÑŸÜÿ™Ÿäÿ¨ÿ©
            if (!result.secure_url) {
              reject(new Error('ŸÑŸÖ Ÿäÿ™ŸÖ ÿ•ÿ±ÿ¨ÿßÿπ ÿ±ÿßÿ®ÿ∑ ÿ¢ŸÖŸÜ ŸÖŸÜ Cloudinary'));
              return;
            }

            resolve(result as CloudinaryUploadResult);
          } else {
            reject(new Error('ŸÑŸÖ Ÿäÿ™ŸÖ ÿ•ÿ±ÿ¨ÿßÿπ ŸÜÿ™Ÿäÿ¨ÿ© ŸÖŸÜ Cloudinary'));
          }
        }
      ).end(buffer);
    });

    return result;
  } catch (error) {
    console.error('‚ùå ÿÆÿ∑ÿ£ ŸÅŸä ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ:', error);

    // ÿ±ÿ≥ÿßÿ¶ŸÑ ÿÆÿ∑ÿ£ ŸÖÿÆÿµÿµÿ© ÿ≠ÿ≥ÿ® ŸÜŸàÿπ ÿßŸÑÿÆÿ∑ÿ£
    let errorMessage = 'ÿÆÿ∑ÿ£ ÿ∫Ÿäÿ± ŸÖÿπÿ±ŸàŸÅ';

    if (error instanceof Error) {
      if (error.message.includes('Invalid cloud_name')) {
        errorMessage = 'ÿßÿ≥ŸÖ Cloud ÿ∫Ÿäÿ± ÿµÿ≠Ÿäÿ≠ ŸÅŸä ÿ•ÿπÿØÿßÿØÿßÿ™ Cloudinary';
      } else if (error.message.includes('Invalid API key')) {
        errorMessage = 'API Key ÿ∫Ÿäÿ± ÿµÿ≠Ÿäÿ≠ ŸÅŸä ÿ•ÿπÿØÿßÿØÿßÿ™ Cloudinary';
      } else if (error.message.includes('file size')) {
        errorMessage = 'ÿ≠ÿ¨ŸÖ ÿßŸÑŸÖŸÑŸÅ ŸÉÿ®Ÿäÿ± ÿ¨ÿØÿßŸã. ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ŸÇÿµŸâ ŸÑŸÑŸÅŸäÿØŸäŸà 100MB';
      } else if (error.message.includes('timeout')) {
        errorMessage = 'ÿßŸÜÿ™Ÿáÿ™ ŸÖŸáŸÑÿ© ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ. ÿ¨ÿ±ÿ® ŸÖŸÑŸÅ ÿ£ÿµÿ∫ÿ±';
      } else if (isVideo && error.message.includes('resource_type')) {
        errorMessage = 'ŸÜŸàÿπ ÿßŸÑŸÅŸäÿØŸäŸà ÿ∫Ÿäÿ± ŸÖÿØÿπŸàŸÖ. ÿ¨ÿ±ÿ® MP4';
      } else {
        errorMessage = error.message;
      }
    }

    throw new Error(`ŸÅÿ¥ŸÑ ŸÅŸä ÿ±ŸÅÿπ ÿßŸÑŸÖŸÑŸÅ: ${errorMessage}`);
  }
}

/**
 * ÿ≠ÿ∞ŸÅ ŸÖŸÑŸÅ ŸÖŸÜ Cloudinary
 */
export async function deleteFromCloudinary(
  publicId: string,
  resourceType: 'image' | 'video' | 'raw' = 'image'
): Promise<any> {
  try {
    console.log('üóëÔ∏è ÿ≠ÿ∞ŸÅ ŸÖŸÑŸÅ ŸÖŸÜ Cloudinary:', publicId);

    const result = await cloudinary.uploader.destroy(publicId, {
      resource_type: resourceType,
      invalidate: true
    });

    console.log('‚úÖ ÿ™ŸÖ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖŸÑŸÅ:', result);
    return result;
  } catch (error) {
    console.error('‚ùå ÿÆÿ∑ÿ£ ŸÅŸä ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖŸÑŸÅ:', error);
    throw error;
  }
}

/**
 * ÿßŸÑÿ≠ÿµŸàŸÑ ÿπŸÑŸâ ÿ±ÿßÿ®ÿ∑ ŸÖÿ≠ÿ≥ŸÜ ŸÑŸÑÿµŸàÿ±ÿ©
 */
export function getOptimizedImageUrl(
  publicId: string,
  options: Record<string, unknown> = {}
): string {
  return cloudinary.url(publicId, {
    quality: options.quality || 'auto',
    fetch_format: options.format || 'auto',
    width: options.width,
    height: options.height,
    crop: options.crop || 'fill',
    flags: 'progressive',
    ...options
  });
}

/**
 * ÿßŸÑÿ≠ÿµŸàŸÑ ÿπŸÑŸâ ÿ±ÿßÿ®ÿ∑ ŸÖÿ≠ÿ≥ŸÜ ŸÑŸÑŸÅŸäÿØŸäŸà
 */
export function getOptimizedVideoUrl(
  publicId: string,
  options: Record<string, unknown> = {}
): string {
  return cloudinary.url(publicId, {
    resource_type: 'video',
    quality: options.quality || 'auto',
    fetch_format: options.format || 'auto',
    width: options.width,
    height: options.height,
    ...options
  });
}

/**
 * ÿ•ŸÜÿ¥ÿßÿ° thumbnail ŸÑŸÑŸÅŸäÿØŸäŸà
 */
export function getVideoThumbnail(
  publicId: string,
  options: Record<string, unknown> = {}
): string {
  return cloudinary.url(publicId, {
    resource_type: 'video',
    format: 'jpg',
    quality: 'auto',
    width: options.width || 300,
    height: options.height || 200,
    crop: 'fill',
    start_offset: options.start_offset || '0',
    ...options
  });
}

/**
 * ÿ™ÿ≠ÿ≥ŸäŸÜ ÿßŸÑÿµŸàÿ±ÿ© ÿ™ŸÑŸÇÿßÿ¶ŸäÿßŸã ÿ≠ÿ≥ÿ® ÿßŸÑÿßÿ≥ÿ™ÿÆÿØÿßŸÖ
 */
export function getResponsiveImageUrl(
  publicId: string,
  usage: 'thumbnail' | 'card' | 'hero' | 'gallery' = 'card'
): string {
  const configs = {
    thumbnail: { width: 150, height: 150, crop: 'thumb', gravity: 'face' },
    card: { width: 400, height: 300, crop: 'fill' },
    hero: { width: 1200, height: 600, crop: 'fill' },
    gallery: { width: 800, height: 600, crop: 'fill' }
  };

  const config = configs[usage];

  return cloudinary.url(publicId, {
    ...config,
    quality: 'auto',
    fetch_format: 'auto',
    flags: 'progressive',
    dpr: 'auto'
  });
}

/**
 * ÿ•ŸÜÿ¥ÿßÿ° ŸÖÿ¨ŸÖŸàÿπÿ© ŸÖŸÜ ÿßŸÑÿµŸàÿ± ÿ®ÿ£ÿ≠ÿ¨ÿßŸÖ ŸÖÿÆÿ™ŸÑŸÅÿ© (responsive images)
 */
export function getImageSrcSet(publicId: string): string {
  const sizes = [320, 640, 768, 1024, 1280, 1920];

  return sizes.map(size => {
    const url = cloudinary.url(publicId, {
      width: size,
      quality: 'auto',
      fetch_format: 'auto',
      flags: 'progressive'
    });
    return `${url} ${size}w`;
  }).join(', ');
}

/**
 * ÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿµÿ≠ÿ© ÿ±ÿßÿ®ÿ∑ Cloudinary
 */
export function isCloudinaryUrl(url: string): boolean {
  return url.includes('cloudinary.com') || url.includes('res.cloudinary.com');
}

/**
 * ÿßÿ≥ÿ™ÿÆÿ±ÿßÿ¨ ŸÖÿπÿ±ŸÅ ÿßŸÑÿπÿßŸÖ ŸÖŸÜ ÿ±ÿßÿ®ÿ∑ Cloudinary
 */
export function extractPublicIdFromUrl(url: string): string | null {
  if (!isCloudinaryUrl(url)) return null;

  const matches = url.match(/\/(?:image|video)\/upload\/(?:v\d+\/)?(.+?)(?:\.|$)/);
  return matches ? matches[1].split('.')[0] : null;
}

export default cloudinary;